/* Dashboard Container */
.dashboard {
  padding: 2rem;
  min-height: 100vh;
  /* background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%); */
  font-family: "Poppins", sans-serif;
  /* Ensure proper positioning within the layout */
  width: 100%;
  box-sizing: border-box;
  /* Prevent horizontal overflow */
  overflow-x: hidden;
}

/* RTL Support */
.dashboard.rtl {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  direction: rtl;
}

/* Ensure content is properly aligned within the layout */
.dashboard-content {
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  /* Prevent horizontal overflow */
  overflow-x: hidden;
}

/* Loading State */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid #e5e7eb;
  border-top: 4px solid #B68A35;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-container p {
  color: #6b7280;
  font-size: 1.125rem;
  margin: 0;
}

/* Error State */
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  text-align: center;
  padding: 2rem;
}

.error-icon {
  color: #ef4444;
  margin-bottom: 1.5rem;
}

.error-container h3 {
  color: #111827;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.error-container p {
  color: #6b7280;
  font-size: 1.125rem;
  margin-bottom: 2rem;
  max-width: 500px;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  min-width: 100px;
}

.btn-primary {
  background: #B68A35;
  color: white;
}

.btn-primary:hover {
  background: #B68A35;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.3);
}

/* Dashboard Header */
.dashboard-header {
  text-align: center;
  margin-bottom: 2rem;
}

.dashboard-header h1 {
  color: #5F646D;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.dashboard-subtitle {
  color: #6b7280;
  font-size: 1rem;
  margin: 0;
  font-weight: 400;
}

/* Metrics Grid - Compact Design */
.metrics-grid.compact {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
  width: 100%;
  max-width: 100%;
  border-radius: 16px;
  padding: 1.5rem;
  overflow-x: hidden;
}

.metric-card.compact {
  border-radius: 20px;
  padding: 1.5rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(27, 29, 33, 0.08);
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}

.metric-card.compact::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  padding: 2px;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.1) 0%,
    rgba(27, 29, 33, 0.1) 25%,
    rgba(52, 73, 94, 0.1) 50%,
    rgba(229, 75, 29, 0.1) 75%,
    rgba(212, 170, 58, 0.1) 100%
  );
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: xor;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
}

.metric-card.compact::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.05) 0%,
    rgba(27, 29, 33, 0.05) 25%,
    rgba(52, 73, 94, 0.05) 50%,
    rgba(229, 75, 29, 0.05) 75%,
    rgba(212, 170, 58, 0.05) 100%
  );
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 0;
}

.metric-card.compact:hover {
  transform: translateY(-6px) scale(1.02);

  background: linear-gradient(135deg, #ffffff 0%, #f1f5f9 100%);
}

.metric-card.compact:hover::before {
  opacity: 1;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.3) 0%,
    rgba(27, 29, 33, 0.3) 25%,
    rgba(52, 73, 94, 0.3) 50%,
    rgba(229, 75, 29, 0.3) 75%,
    rgba(212, 170, 58, 0.3) 100%
  );
  animation: borderGlow 2s ease-in-out infinite alternate;
}

.metric-card.compact:hover::after {
  opacity: 1;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.08) 0%,
    rgba(27, 29, 33, 0.08) 25%,
    rgba(52, 73, 94, 0.08) 50%,
    rgba(229, 75, 29, 0.08) 75%,
    rgba(212, 170, 58, 0.08) 100%
  );
}

@keyframes borderGlow {
  0% {
    background: linear-gradient(
      135deg,
      rgba(212, 170, 58, 0.3) 0%,
      rgba(27, 29, 33, 0.3) 25%,
      rgba(52, 73, 94, 0.3) 50%,
      rgba(229, 75, 29, 0.3) 75%,
      rgba(212, 170, 58, 0.3) 100%
    );
  }
  100% {
    background: linear-gradient(
      135deg,
      rgba(229, 75, 29, 0.3) 0%,
      rgba(212, 170, 58, 0.3) 25%,
      rgba(27, 29, 33, 0.3) 50%,
      rgba(52, 73, 94, 0.3) 75%,
      rgba(229, 75, 29, 0.3) 100%
    );
  }
}

.metric-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.1),
    rgba(27, 29, 33, 0.1)
  );
  border-radius: 18px;
  margin-bottom: 1.25rem;
  color: #1B1D21;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  z-index: 2;
}

.metric-icon::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-radius: 20px;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.2) 0%,
    rgba(27, 29, 33, 0.2) 50%,
    rgba(52, 73, 94, 0.2) 100%
  );
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: -1;
}

.metric-card.compact:hover .metric-icon {
  transform: scale(1.1) rotate(5deg);
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.2),
    rgba(27, 29, 33, 0.2)
  );
  color: #1B1D21;
  box-shadow: 0 8px 25px rgba(212, 170, 58, 0.2);
}

.metric-card.compact:hover .metric-icon::before {
  opacity: 1;
  background: linear-gradient(
    135deg,
    rgba(212, 170, 58, 0.3) 0%,
    rgba(27, 29, 33, 0.3) 50%,
    rgba(52, 73, 94, 0.3) 100%
  );
  animation: iconGlow 1.5s ease-in-out infinite alternate;
}

@keyframes iconGlow {
  0% {
    box-shadow: 0 0 10px rgba(212, 170, 58, 0.3);
  }
  100% {
    box-shadow: 0 0 20px rgba(52, 73, 94, 0.4);
  }
}

.metric-content {
  position: relative;
  z-index: 2;
}

.metric-content h3 {
  color: #1B1D21;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  margin-top: 0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.metric-numbers {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.metric-main {
  font-size: 2rem;
  font-weight: 700;
  color: #5F646D;
  line-height: 1;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.metric-main::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #B68A35, #34495E);
  transition: width 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.metric-card.compact:hover .metric-content h3 {
  color: #1B1D21;
  transform: translateY(-2px);
}

.metric-card.compact:hover .metric-main {
  color: #1B1D21;
  transform: scale(1.05);
}

.metric-card.compact:hover .metric-main::after {
  width: 100%;
}

.metric-details {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}

.metric-description {
  margin-top: 0.75rem;
}

.metric-description p {
  color: #6b7280;
  font-size: 0.75rem;
  line-height: 1.4;
  margin: 0;
  font-weight: 400;
}

.metric-item {
  font-size: 0.75rem;
  font-weight: 500;
  padding: 0.25rem 0.5rem;
  border-radius: 999px;
  background: rgba(0, 0, 0, 0.05);
}

/* Monthly Visits Section - Full Width */
.monthly-visits-section {
  margin-bottom: 2rem;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

.chart-card.full-width {
  width: 100%;
  margin: 0;
  max-width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.chart-container.full-width {
