/* Responsive Design */
@media (max-width: 1024px) {
  .charts-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1rem;
  }
}

@media (max-width: 1024px) {
  .visits-charts-grid {
    grid-template-columns: 1fr 1fr;
    gap: 1.25rem;
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
  }

  .visits-charts-grid .chart-card:nth-child(3) {
    grid-column: 1 / -1;
  }
}

@media (max-width: 768px) {
  .dashboard {
    padding: 0.75rem;
    overflow-x: hidden;
  }

  .dashboard-content {
    overflow-x: hidden;
    width: 100%;
    max-width: 100%;
  }

  .dashboard-header h1 {
    font-size: 1.5rem;
  }

  .metrics-grid.compact {
