    gap: 0.75rem;
    width: 100%;
    max-width: 100%;
  }

  .employees-row {
    grid-template-columns: 1fr;
    gap: 1rem;
    width: 100%;
    max-width: 100%;
  }

  .charts-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
    width: 100%;
    max-width: 100%;
  }

  /* Force all chart cards to take full width and prevent overflow */
  .chart-card {
    padding: 1rem;
    width: 100% !important;
    max-width: 100% !important;
    box-sizing: border-box;
    overflow: hidden;
  }

  /* Specific responsive styles for the four specified cards */
  .charts-grid .chart-card {
    min-width: 0;
    width: 100% !important;
    max-width: 100% !important;
  }

  /* Specific responsive styles for the four specified cards */
  .charts-grid .chart-card {
    min-width: 0;
    width: 100% !important;
    max-width: 100% !important;
  }

  /* Force chart containers to be responsive */
  .chart-container {
    height: 220px;
    width: 100% !important;
    max-width: 100% !important;
    overflow: hidden;
    box-sizing: border-box;
  }

  .chart-container.large {
    height: 250px;
  }

  .chart-container.full-width {
    height: 200px;
  }

  .chart-container.compact {
    height: 150px;
